/**
 * api接口统一管理
 */
import { postJsonRequest } from './http'

export const pyzApi = {
  postMessage: function (message) {
    const url = 'https://qyapi.weixin.qq.com/cgi-bin/webhook/send?key=e3e0a167-35df-474b-96d1-de520ad662ae'
    return postJsonRequest(url, {
      "msgtype": "markdown",
      "markdown": {
        "content": `<font color="comment">${message}</font>
           >发自<font color="warning">xx</font>`
      }
    })
  }
}
