import Vue from 'vue'
Vue.filter('formatDate', function (date, type) {
  if (date !== '0') {
    if (date === '' || date === undefined || date === null) {
      return '--'
    }
  }
  let dateTime = '格式化失败'
  let getDate = date
  if (!(getDate instanceof Date)) {
    getDate = new Date(date)
  }
  const year = getDate.getFullYear()
  const month = getDate.getMonth() + 1
  const day = getDate.getDate()
  if (type === 'yyyy-MM-dd') {
    dateTime = year + '-' + addZero(month) + '-' + addZero(day)
  } else if (type === 'yyyy-M-d') {
    dateTime = year + '-' + month + '-' + day
  } else {
    const hours = getDate.getHours()
    const minutes = getDate.getMinutes()
    dateTime = year + '-' + addZero(month) + '-' + addZero(day) +
      ' ' + addZero(hours) + ':' + addZero(minutes)
  }
  return dateTime
})

Vue.filter('shortTitle', function (title, max) {
  if (title && title.length > max) {
    return title.substring(0, max) + '...'
  } else {
    return title
  }
})

Vue.filter('fileSizeName', function (value) {
  if (value === null || value === '' || value === 0) {
    return '--'
  }
  const unitArr = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB']
  let index = 0
  const srcsize = parseFloat(value)
  index = Math.floor(Math.log(srcsize) / Math.log(1024))
  let size = srcsize / Math.pow(1024, index)
  size = size.toFixed(2)// 保留的小数位数
  const sIndex = size.indexOf('.00')
  if (sIndex !== -1) {
    size = size.substring(0, sIndex)
  }
  return size + unitArr[index]
})

function addZero (val) {
  if (val < 10) {
    return '0' + val
  } else {
    return val
  }
}
