import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// 全局单例存储

const store = new Vuex.Store({
  state: {
    user: null
  },
  mutations: {
    login (state, user) {
      state.user = user
    }
  },
  getters: {
    isTest: function () {
      return process.env.NODE_ENV !== 'production'
    },
    isLogin: function (state) {
      return state.user !== null && state.user !== undefined
    }
  }
})

export default store
